
.header {
  background-color: #668;
  width: 100%;
  height: 50px;
  position: relative;
}

.header > h1 {
  top: -7px;
  left: 50px;
  position: absolute;
}

.Goals{
  margin: 20px;
  padding: 20px 20px;

  width: fit-content;

  border-style: solid;
  border-color: #668;
  border-radius: 10px;
}
.Goals > h2, p{
  display: inline-block;
  text-indent: 5px;
  margin: 0px;
}
.Goals > ul{
  margin: 10px 0px 0px 0px;
  padding: 0px 0px 0px 7px;
  list-style: none;
}